html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


/* Основной контейнер */
.calculator-container {
  min-height: 100vh;
  background: linear-gradient(to right, #e0ecff, #ffffff);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  font-family: 'Segoe UI', sans-serif;
}

/* Карточка */
.calculator-box {
  background-color: #fff;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  max-width: 420px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.calculator-title {
  font-size: 1.75rem;
  font-weight: bold;
  color: #1e3a8a;
  text-align: center;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

label {
  display: flex;
  flex-direction: column;
  font-size: 0.95rem;
  color: #374151;
  gap: 0.3rem;
}

.calculator-input {
  width: 100%;
  border: 1px solid #ccc;
  padding: 0.6rem;
  border-radius: 0.5rem;
  font-size: 1rem;
}

.calculate-button {
  width: 100%;
  background-color: #2563eb;
  color: white;
  padding: 0.75rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.3s ease;
  margin-top: 0.5rem;
}

.calculate-button:hover {
  background-color: #1d4ed8;
}

.result-box {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  padding: 1rem;
  border-radius: 1rem;
  font-size: 0.95rem;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.result-final {
  font-size: 1.25rem;
  color: #059669;
  font-weight: bold;
  margin-top: 0.8rem;
}

.disclaimer {
  font-size: 0.75rem;
  color: #6b7280;
  text-align: center;
  line-height: 1.4;
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px dashed #d1d5db;
}
